@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rammetto+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 767px){h1{font-size:30px}}@media (max-width: 767px){h5{font-size:16px}}@media (max-width: 767px){h3{font-size:20px}}.Home{background-color:#080522;min-height:100vh;color:#fff;font-family:"Kanit", sans-serif;overflow-x:hidden}.Home .headings{text-align:center;font-family:"Righteous", cursive;margin:30px}.Home .navbar{font-size:18px;font-family:"Kanit", sans-serif;background-color:transparent}.Home .navbar .navBrand{font-size:40px;font-family:"Fredoka One", cursive;color:#fff}.Home .navbar .navLinks{color:#fff}.Home .navbar .navLinks:hover{color:#f0aac1}.Home .Banner{display:flex;flex-direction:column;align-items:center;height:800px;justify-content:center}.Home .Banner h1{text-align:center;font-size:40px;font-family:"Rammetto One", cursive}@media (max-width: 767px){.Home .Banner h1{font-size:30px}}.Home .Banner h5{text-align:center;font-family:"Kanit", sans-serif;margin:10px}.Home .Banner .btn-explore{width:300px;background:#fff;color:#c61752;border:none;border-radius:25px;padding:10px;font-family:"Righteous", cursive}.Home .Banner .btn-explore:hover{background:#e31657;color:#fff}.Home .Banner .welcomeImg{height:300px}@media (max-width: 767px){.Home .Banner .welcomeImg{height:250px}}.Home #Services{display:flex;justify-content:space-around;flex-wrap:wrap}.Home #Services .ServiceCard{height:300px;width:300px;background:#fff;padding:20px;text-align:center;color:#080522;transform:scale(1);transition:0.5s;margin:20px}.Home #Services .ServiceCard .Image{height:250px;width:250px}.Home #Services .ServiceCard:hover{cursor:pointer;transform:scale(1.1);box-shadow:0 0 120px rgba(225,225,225,0.5)}.Home .Projects{font-family:-apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:default;min-height:500px;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;padding-top:50px;color:#fff}.Home .Projects div{text-align:center;font-family:"Concert One", cursive;margin-left:20px;margin-right:20px}.Home .Projects div h2{padding:20px}

.card{width:45ch;height:45ch;background:grey;border-radius:5px;background:#f0f0f0;background-size:cover;background-position:center center;box-shadow:0px 10px 30px -5px rgba(0,0,0,0.3);transition:box-shadow 0.5s;will-change:transform;border:15px solid white}.card:hover{box-shadow:0px 30px 100px -10px rgba(0,0,0,0.4)}




