//Variables Decleration
$tablet: "(min-width: 768px) and (max-width: 1023px)";
$desktop: "(min-width: 1024px)";
$mobile: "(max-width:767px)";

@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kanit&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Righteous&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rammetto+One&display=swap");
@import "../../typography.scss";

.Home {
    background-color: #080522;
    min-height: 100vh;
    color: #fff;
    font-family: "Kanit", sans-serif;
    overflow-x: hidden;
    .headings {
        text-align: center;
        font-family: "Righteous", cursive;
        margin: 30px;
    }
    .navbar {
        font-size: 18px;
        font-family: "Kanit", sans-serif;
        background-color: transparent;
        .navBrand {
            font-size: 40px;
            font-family: "Fredoka One", cursive;
            color: #fff;
        }
        .navLinks {
            color: #fff;
            &:hover {
                color: rgb(240, 170, 193);
            }
        }
    }
    .Banner {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 800px;
        justify-content: center;
        h1 {
            text-align: center;
            font-size: 40px;
            font-family: "Rammetto One", cursive;
            @media #{$mobile} {
                font-size: 30px;
            }
        }
        h5 {
            text-align: center;
            font-family: "Kanit", sans-serif;
            margin: 10px;
        }
        .btn-explore {
            width: 300px;
            background: #fff;
            color: #c61752;
            border: none;
            border-radius: 25px;
            padding: 10px;
            font-family: "Righteous", cursive;
            &:hover {
                background: #e31657;
                color: #fff;
            }
        }
        .welcomeImg {
            height: 300px;
            @media #{$mobile} {
                height: 250px;
            }
        }
    }
    #Services {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        .ServiceCard {
            height: 300px;
            width: 300px;
            background: #fff;
            padding: 20px;
            text-align: center;
            color: #080522;
            transform: scale(1);
            transition: 0.5s;
            margin: 20px;
            .Image {
                height: 250px;
                width: 250px;
            }
            &:hover {
                cursor: pointer;
                transform: scale(1.1);
                box-shadow: 0 0 120px rgba(225, 225, 225, 0.5);
            }
        }
    }
    .Projects {
        font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto,
            noto, segoe ui, arial, sans-serif;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        cursor: default;
        min-height: 500px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        padding-top: 50px;
        color: #fff;
        div {
            text-align: center;
            font-family: "Concert One", cursive;
            margin-left:20px;
            margin-right:20px;
            h2 {
                padding: 20px;
            }
        }
    }
}
